export default {
  props: ["data"],
  methods: {
    exportDetailFn: function exportDetailFn() {
      var _this = this;

      this.data.loading = true;

      if ($("#ifilePro").length == 0) {
        $("body").append('<iframe id="ifilePro" style="display:none"></iframe>');
      }

      this.$apiCall('api.AccountPayment.exportPayment', {
        ids: this.data.ids,
        isNew: 1
      }, function (r) {
        _this.data.loading = false;

        if (r.ErrorCode == "9999") {
          document.getElementById("ifilePro").src = r.Data.Results.file;

          _this.$elementMessage('Export success', 'success');

          _this.data.isShow = false;
        } else {
          _this.$elementMessage(r.Message, 'error');
        }
      });
    }
  }
};