import DialogDetail from '@/components/dialogDetail.vue';
export default {
  data: function data() {
    return {
      loading: false,
      pageSizes: [10, 20, 50, 100],
      page: this.$route.query.page ? Number(this.$route.query.page) : 1,
      rowsPerPage: localStorage.getItem("c_transactionRowsPerPage") ? Number(localStorage.getItem("c_transactionRowsPerPage")) : 10,
      items: [],
      total: 0,
      totalPage: 0,
      filterParams: {
        verdorName: "",
        paymentCode: "",
        code: "",
        accountPayee: "",
        tradeStatus: "",
        tradeType: "",
        date: [],
        timeCreatedFrom: "",
        timeCreatedTo: "",
        shopifyOrder: ""
      },
      filterParamsDefault: "{}",
      dateFilter: "",
      viewDetail: {
        isShow: false,
        exportLoading: false,
        items: []
      },
      tradeTypeArr: [{
        id: 1,
        name: "Payment order"
      }, {
        id: 6,
        name: "Refund order"
      }, {
        id: 7,
        name: "Wallet card"
      }, {
        id: 8,
        name: "Manual deduction"
      }],
      tradeStatusArr: [{
        id: 1,
        name: "Paying"
      }, {
        id: 2,
        name: "Account paid"
      }, {
        id: 3,
        name: "Refunded"
      }, {
        id: 9,
        name: "Cancel"
      }],
      accountPayeeArr: [{
        id: 1,
        name: "stripe"
      }, {
        id: 2,
        name: "ebanx"
      }, {
        id: 3,
        name: "PayPal"
      }, {
        id: 4,
        name: "checkout"
      }, {
        id: 6,
        name: "Bonus balance"
      }, {
        id: 10,
        name: "dLocal"
      }, {
        id: 11,
        name: "PAYONEER"
      }, {
        id: 12,
        name: "Offline payment"
      }],
      checkItems: [],
      dialogDetailData: {
        isShow: false,
        loading: false,
        ids: [],
        items: []
      },
      defaultDialogDetailData: '{}'
    };
  },
  components: {
    DialogDetail: DialogDetail
  },
  watch: {
    $route: "gotoPage"
  },
  mounted: function mounted() {
    this.defaultDialogDetailData = JSON.stringify(this.dialogDetailData);
    this.filterParamsDefault = JSON.stringify(this.filterParams);
    this.getItem();
  },
  methods: {
    openExportDetail: function openExportDetail(item) {
      var _this = this;

      this.loading = true;
      this.$apiCall('api.AccountPayment.getInvoiceShopifyInfo', {
        id: item.id
      }, function (r) {
        _this.loading = false;

        if (r.ErrorCode == "9999") {
          _this.dialogDetailData = JSON.parse(_this.defaultDialogDetailData);
          _this.dialogDetailData.ids = [item.id];
          _this.dialogDetailData.items = Object.keys(r.Data.Results).map(function (k) {
            return r.Data.Results[k];
          });
          _this.dialogDetailData.isShow = true;
        } else {
          _this.$elementMessage(r.Message, 'error');
        }
      });
    },
    exportDetailFn: function exportDetailFn() {
      var _this2 = this;

      var ids = [];
      if (!this.viewDetail.items.length) return;
      this.viewDetail.items.forEach(function (item) {
        ids.push(item.id);
      });
      this.viewDetail.exportLoading = true;

      if ($("#ifilePro").length == 0) {
        $("body").append('<iframe id="ifilePro" style="display:none"></iframe>');
      }

      var openDownload = function openDownload(url) {
        document.getElementById("ifilePro").src = url;
      };

      var apiUrl = "api.AccountPayment.exportPayment";
      this.$apiCall(apiUrl, {
        ids: ids
      }, function (r) {
        _this2.viewDetail.exportLoading = false;

        if (r.ErrorCode == "9999") {
          _this2.$elementMessage('Export success', 'success');

          openDownload(r.Data.Results.file);
        } else {
          _this2.$elementMessage(r.Message, 'error');

          if (r.ErrorCode == "1002" || r.ErrorCode == "1003") {
            _this2.$userFailure(_this2);
          }
        }
      });
    },
    // val表示选中的表格行数据
    handleSelectionChange: function handleSelectionChange(val) {
      this.viewDetail.items = [];
      this.checkItems = [];
      if (!val.length) return;
      this.viewDetail.items = val;
      this.checkItems = val;
    },
    viewDetailFn: function viewDetailFn(type, item) {
      this.viewDetail.isShow = true;

      if (type == 'row') {
        this.viewDetail.items = [];
        this.viewDetail.items.push(item);
      }
    },
    filterText: function filterText(type, id) {
      var obj;

      switch (type) {
        case 'tradeType':
          this.tradeTypeArr.forEach(function (item) {
            if (item.id == id) obj = item;
          });
          break;

        case 'tradeStatus':
          this.tradeStatusArr.forEach(function (item) {
            if (item.id == id) obj = item;
          });
          break;

        case 'platformType':
          this.accountPayeeArr.forEach(function (item) {
            if (item.id == id) obj = item;
          });
          break;

        default:
          break;
      }

      return obj ? obj.name : "---";
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) this.$router.push({
        query: {
          page: val
        }
      });
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_transactionRowsPerPage", val);
      this.getItem();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? Number(this.$route.query.page) : 1;
      this.getItem();
    },
    filterDate: function filterDate() {
      var type = Number(this.dateFilter),
          today = moment().format('YYYY-MM-DD'),
          yewtoday = moment().add(-1, 'days').format('YYYY-MM-DD'),
          last7 = moment().add(-6, 'days').format('YYYY-MM-DD'),
          last30 = moment().add(-29, 'days').format('YYYY-MM-DD');
      this.filterParams.timeCreatedTo = today;

      switch (type) {
        case 1:
          this.filterParams.timeCreatedFrom = today;
          break;

        case 2:
          this.filterParams.timeCreatedFrom = yewtoday;
          this.filterParams.timeCreatedTo = yewtoday;
          break;

        case 3:
          this.filterParams.timeCreatedFrom = last7;
          break;

        case 4:
          this.filterParams.timeCreatedFrom = last30;
          break;

        default:
          break;
      }

      this.filterItem();
    },
    dateChange: function dateChange() {
      if (this.filterParams.date.length > 1) {
        this.filterParams.timeCreatedFrom = moment(this.filterParams.date[0]).format("YYYY-MM-DD");
        this.filterParams.timeCreatedTo = moment(this.filterParams.date[1]).format("YYYY-MM-DD");
        this.filterItem();
      }
    },
    clearFilter: function clearFilter(type) {
      switch (type) {
        case "shopifyOrder":
          this.filterParams.shopifyOrder = "";
          break;

        case "paymentCode":
          this.filterParams.paymentCode = "";
          break;

        case "code":
          this.filterParams.code = "";
          break;

        case "verdorName":
          this.filterParams.verdorName = "";
          break;

        case "tradeType":
          this.filterParams.tradeType = "";
          break;

        case "tradeStatus":
          this.filterParams.tradeStatus = "";
          break;

        case "accountPayee":
          this.filterParams.accountPayee = "";
          break;

        case "date":
          this.filterParams.date = [];
          this.filterParams.timeCreatedFrom = "";
          this.filterParams.timeCreatedTo = "";
          break;

        default:
          this.dateFilter = "";
          this.filterParams = JSON.parse(this.filterParamsDefault);
          break;
      }

      this.filterItem();
    },
    filterItem: function filterItem(type) {
      this.$refs.gridTable.clearSelection();

      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    getItem: function getItem(s) {
      var _this3 = this;

      this.loading = true;
      this.$apiCall("api.AccountPayment.findByCustomer", {
        page: this.page,
        rowsPerPage: this.rowsPerPage,
        timeCreatedFrom: this.filterParams.timeCreatedFrom,
        timeCreatedTo: this.filterParams.timeCreatedTo,
        sysCode: this.filterParams.paymentCode,
        code: this.filterParams.code,
        platformType: this.filterParams.accountPayee,
        paymentType: this.filterParams.tradeType,
        status: this.filterParams.tradeStatus,
        shopifyOrder: this.filterParams.shopifyOrder
