        _this3.loading = false;

        if (r.ErrorCode == 9999) {
          _this3.items = r.Data.Results;
          _this3.total = Number(r.Data.Pagination.totalCount);
          _this3.totalPage = Number(r.Data.Pagination.totalPage);
        } else {
          _this3.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    }
  }
};